import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'

const CssExpose = resolve => require.ensure([], () => resolve(require('@/pages/index')), 'index')
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: { path: '/css-expose/main' }
    },
    {
      path: '/css-expose/',
      name: 'CssExpose',
      component: CssExpose,
      children: [
        {
          path: 'main',
          component: Main,
          name: 'Main'
        }
      ]
    }
  ]
})



